import React, { Fragment } from 'react';
import { Carousel, Image } from 'react-bootstrap';
import { getPlaceholderImg, ProjectColor } from '../../Modules/Common';
import { WorkDetails } from '../../Modules/ProjectDetails';

interface ProfessionalItemProps {
	details: WorkDetails
	withBg?: boolean
}

const WorkBanner = ({
	details,
	withBg,
}: ProfessionalItemProps): JSX.Element => {

	return (
		<Fragment>
			<Image
				alt={`Image of "${details.title}"`}
				width={1280} height={720}
				rounded fluid
				src={details.bannerImg ?? getPlaceholderImg(ProjectColor.GenoaGreen)}
				className='carousel-image'
			/>
			<Carousel.Caption className={withBg ? 'carousel-caption-bg' : ''} >
				<h3>{details.title}</h3>
				<p>{details.subtitle}</p>
			</Carousel.Caption>
		</Fragment>
	);
};

export default WorkBanner;