import React, { Fragment, useState } from 'react';
import { Carousel } from 'react-bootstrap';
import { throneOfLiesDetails, WorkDetails, iSaveLivesDetails } from '../../Modules/ProjectDetails';
import ProjectModal from './ProjectModal/ProjectModal';
import WorkBanner from './WorkBanner';

const WorkContainer = (): JSX.Element => {

	const [isModalVisible, setModalVisible] = useState(false);
	const [isPaused, setPaused] = useState(false);
	const [selectedDetails, setSelectedDetails] = useState(throneOfLiesDetails);

	const handleClick = (details: WorkDetails) => {
		setSelectedDetails(details);
		setPaused(true);
		setModalVisible(true);
	};

	const handleHide = () => {
		setPaused(false);
		setModalVisible(false);
	};

	return (
		<Fragment>
			<ProjectModal show={isModalVisible} handleHide={handleHide} details={selectedDetails} />
			<Carousel interval={isPaused ? null : 3000} >
				<Carousel.Item className='clickable' onClick={() => handleClick(throneOfLiesDetails)}>
					<WorkBanner details={throneOfLiesDetails} />
				</Carousel.Item>
				<Carousel.Item className='clickable' onClick={() => handleClick(iSaveLivesDetails)}>
					<WorkBanner details={iSaveLivesDetails} withBg />
				</Carousel.Item>
			</Carousel>
		</Fragment>
	);
};

export default WorkContainer;