import React from 'react';
import { Modal, Image } from 'react-bootstrap';

const MODAL_PLACEHOLDER = 'https://place-hold.it/1280x720/E97A7F/F3E4D1.png&text=Placeholder&fontsize=120';

interface ProjectModalHeaderProps {
	imageUrl: string | undefined;
}
const ProjectModalHeader = ({ imageUrl }: ProjectModalHeaderProps): JSX.Element => {
		
	return (
		<Modal.Header style={{ justifyContent: 'center' }} className='mx-3 py-3' >
			<Image
				alt='image of the project'
				rounded
				src={imageUrl ?? MODAL_PLACEHOLDER}
				fluid
			/>
		</Modal.Header>
	);
};

export default ProjectModalHeader;