import { ProductType } from './ProjectDetails';

export enum ProjectColor {
	// cold
	BlueCharcoal = '#282c34',
	GenoaGreen = '#337b78',
	JuniperGreen = '#6f9b99',
	PaduaGreen = '#78b992',
	MayaBlue = '#61dafb',
	// warm
	DarkPink = '#e83e8c',
	GeraldinePink = '#e97a7f',
	ProvincialPinkOrange = '#f3e4d1',
	// neutral
	white = 'white'
}

interface PlaceholderImgOptions {
	textColor?: ProjectColor
	text?: string
	fontSize?: number
	width?: number
	height?: number
}

export const getPlaceholderImg = (color = ProjectColor.GenoaGreen, options: PlaceholderImgOptions = {
	textColor: ProjectColor.ProvincialPinkOrange,
	text: 'Placeholder',
	fontSize: 120,
	width: 1280,
	height: 720,
}): string => {

	return `https://place-hold.it/${options.width ?? 1280}x${options.height ?? 720}/${color.slice(1)}/${options.textColor?.slice(1) ?? ProjectColor.ProvincialPinkOrange.slice(1)}.png&text=${options.text ?? 'Placeholder'}&fontsize=${options.fontSize ?? 120}`;
};

export const getProductTypeTxt = (prodType?: ProductType): string => {

	switch (prodType) {
	case ProductType.Steam:
		return 'Steam';

	case ProductType.Github:
		return 'GitHub';

	case ProductType.App:
		return 'Play Store';

	default:
		return '';
	}
};