import React, { Fragment } from 'react';
import { Modal } from 'react-bootstrap';
import { getProductTypeTxt } from '../../../Modules/Common';
import { Project } from '../../../Modules/ProjectDetails';

interface ProjectModalBodyProps {
	project: Project;
}
const ProjectModalBody = ({ project }: ProjectModalBodyProps): JSX.Element => {
	
	const getLanguagesElement = (): JSX.Element | null => {
		if (project.technologies.languages) {
			return (
				<p className='m-0'>Languages: {project.technologies.languages.join(', ')}</p>
			);
		}
		else {
			return null;
		}
	};

	const getFrameworksElement = (): JSX.Element | null => {
		if (project.technologies.frameworks) {
			return (
				<p className='m-0'>Frameworks: {project.technologies.frameworks.join(', ')}</p>
			);
		}
		else {
			return null;
		}
	};

	const getOthersElement = (): JSX.Element | null => {
		if (project.technologies.other) {
			return (
				<p className='m-0'>Other: {project.technologies.other.join(', ')}</p>
			);
		}
		else {
			return null;
		}
	};

	const getTechnologiesElement = (): JSX.Element => {
		
		return (
			<Fragment>
				{getLanguagesElement()}
				{getFrameworksElement()}
				{getOthersElement()}
			</Fragment>
		);
	};

	const getProductElement = (): JSX.Element | null => {
		if (project.product) {
			return (
				<p className='mt-4' style={{ textAlign: 'right' }}>
					<a href={project.product.url} target='_blank' rel='noreferrer' >Visit {getProductTypeTxt(project.product.type)} &gt;</a>
				</p>
			);
		}
		else {
			return null;
		}
	};
		
	return (
		<Modal.Body style={{ justifyContent: 'center' }} className='mt-1 ml-1 mr-1 pb-3' >
			{project.description}
			<hr />
			{getTechnologiesElement()}
			{getProductElement()}
		</Modal.Body>
	);
};

export default ProjectModalBody;