import React, { Fragment } from 'react';
import { Container, Modal } from 'react-bootstrap';
import { Globe, ClipboardCheck } from 'react-bootstrap-icons';
import { PersonalProject, WorkDetails } from '../../../Modules/ProjectDetails';

interface ProjectModalFooterProps {
	project: PersonalProject | WorkDetails;
}
const ProjectModalFooter = ({ project }: ProjectModalFooterProps): JSX.Element | null => {

	const getWebsiteElement = (): JSX.Element | null => {
		if (project.websiteUrl) {
			return (
				<Fragment>
					<Globe className='mb-1' />
					<span className='mx-2'>Website:</span>
					<a href={project.websiteUrl} target='_blank' rel='noreferrer' >{project.websiteUrl}</a>
				</Fragment>
			);
		}
		else {
			return null;
		}
	};

	const getLicenseElement = (): JSX.Element | null => {
		if (project.isPersonal) {
			return (
				<Fragment>
					<ClipboardCheck className='mb-1' />
					<span className='mx-2'>{project.licenseDetails}</span>
				</Fragment>
			);
		}
		else {
			return null;
		}
	};

	if (!project.websiteUrl && !project.isPersonal) {
		return null;
	}
	else {
		return (
			<Modal.Footer style={{ justifyContent: 'flex-start' }}>
				<Container className='p-0' fluid>
					{getWebsiteElement()}
					{getLicenseElement()}
				</Container>
			</Modal.Footer>
		);
	}
};

export default ProjectModalFooter;