import React from 'react';
import { Col } from 'react-bootstrap';
import { AccountDetails } from '../../Modules/AccountDetails';

interface ContactDetailProps {
	AccountDetails: AccountDetails
}

const ContactDetails = ({ AccountDetails }: ContactDetailProps): JSX.Element => {

	return (
		<Col>
			<AccountDetails.icon size='1.4rem' style={{ marginRight: '1rem' }} />
			<a href={AccountDetails.url} target='_blank' rel='noreferrer' >{AccountDetails.displayName}</a>
		</Col>
	);
};

export default ContactDetails;