import React from 'react';
import { Modal } from 'react-bootstrap';
import { getPlaceholderImg, ProjectColor } from '../../../Modules/Common';
import { WorkDetails, PersonalProject } from '../../../Modules/ProjectDetails';
import ProjectModalBody from './ProjectModalBody';
import ProjectModalFooter from './ProjectModalFooter';
import ProjectModalHeader from './ProjectModalHeader';

interface ProjectModalProps {
	show: boolean
	handleHide: () => void
	details: WorkDetails | PersonalProject
}
const ProjectModal = ({ show, handleHide, details }: ProjectModalProps  ): JSX.Element => {

	return (
		<Modal size='xl' show={show} onHide={handleHide} style={{ maxHeight: '100vh' }} centered>
			<ProjectModalHeader imageUrl={details.modalImg ?? getPlaceholderImg(ProjectColor.GenoaGreen)} />
			<ProjectModalBody project={details} />
			<ProjectModalFooter project={details} />
		</Modal>
	);
};

export default ProjectModal;