import React from 'react';
import { Container, Row } from 'react-bootstrap';
import { GithubDetails, TwitterDetails, DiscordDetails, LinkedinDetails, EmailDetails } from '../../Modules/AccountDetails';
import ContactDetails from './ContactDetails';

const PersonalCardFooter = (): JSX.Element => {

	return (
		<Container fluid className='small p-0' style={{ justifyContent: 'space-evenly' }}>
			{/* Socials */}
			<span>Socials:</span>
			<Row className='mb-3' xs='1' lg='4'>
				<ContactDetails AccountDetails={GithubDetails} />
				<ContactDetails AccountDetails={TwitterDetails} />
				<ContactDetails AccountDetails={DiscordDetails} />
				<ContactDetails AccountDetails={LinkedinDetails} />
			</Row>
			{/* Business Inquiries */}
			<span>Business Email:</span>
			<Row>
				<ContactDetails AccountDetails={EmailDetails} />
			</Row>
		</Container>
	);
};

export default PersonalCardFooter;