export const voakDetails: PersonalProject = {
	modalImg: DiscordJSImg,
	title: 'Voice of a Kingdom',
	subtitle: 'Text-Based Discord Game',
	description: (
		<Fragment>
			<h4><strong>Game Description</strong></h4>
			<hr />
			<p>The user is a King that has just started their leadership at a random age in history. Every few in-game months, the user is asked to make a decision for the Kingdom. This decision will always have an impact on some or all of the social groups of the Kingdom.</p>
			<p>Ignoring each group can lead to different consequences, none of which are good. Ideally, the user will try to balance the satisfaction of the groups so they stay out of trouble. However, there is one thing that makes this a little bit more of a challenge than other similar games. On top of trying to balance the satisfaction of social groups, you have to balance your personal stats, like your happiness. Ignoring these can be just as dangerous for you as ignoring the social groups.</p>
			<p>When a social group or a personal stat is ignored it is likely an event will spawn. Not all events are negative and not all events will lead to the end of the game. Even in the case of a negative event, you are often given options that could potentially get you out of trouble temporarily.</p>
			<p>The game either ends when you are too old to rule your Kingdom or you are dead due to old age or due to one of the not-so-pleasant events.</p>
			<hr />
			<h4><strong>The Bot</strong></h4>
			<hr />
			<p>The bot is what this project is really based on. In order for any of this to work, the bot needs to be in the server. Upon starting a game a new channel will be created with permissions only for the bot and the player with an initial message about when their leadership began and other basic information. After that the first months pass and the player is given their first request. The bot constantly checks the stats after each request and decides the chances of an event. On top of that other factors, like the current in-game date, are checked. Before each request is answered, the bot will show the user what each decision will entail as far as stats go, as well the current stats of the user.</p>
			<p>One of my goals for this project was to learn how to make &quot;moddable&quot; games, which lined-up perfectly with my lack of creativity for lore. That said, I figured I&#39;d make the requests moddable and give the users or anyone really, the opportunity to use their own requests using an xml file. Someone might say I never finished this project because of this, but I&#39;ll just call it &quot;moddable&quot; to make myself feel better about it. After all, the functionality is there.</p>
			<p><del>If anyone is willing to find some lore, please help me, I&#39;m desperate.</del></p>
		</Fragment>
	),
	technologies: {
		languages: ['C#'],
		frameworks: ['.NET'],
		other: ['discord.NET', 'XML'],
	},
	product: {
		type: ProductType.Github, url: 'https://github.com/TryphonX/VoiceOfAKingdomDiscord',
	},
	licenseDetails: 'BSD 3-Clause @ 2020 Tryphon Ksydas',
	isPersonal: true,
};

