import React, { Fragment } from 'react';
import { Button, Container } from 'react-bootstrap';

interface HomeSectionProps {
	className?: string
	prev?: SectionInfo
	id: string
	next?: SectionInfo
	children?: JSX.Element | JSX.Element[]
}

interface SectionInfo {
	id: string
	label: string
}

const HomeSection = ({className, prev, id, next, children}: HomeSectionProps): JSX.Element => {

	const getPrevButton = () => {

		if (!prev) return;

		return (
			<Button
				className='rounded-pill btn-fab'
				size='lg'
				onClick={() => document.getElementById(prev.id)?.scrollIntoView()}
			>
				{prev.label}
			</Button>
		);
	};

	const getNextButton = () => {

		if (!next) return;

		return (
			<Button
				className='rounded-pill btn-fab'
				size='lg'
				onClick={() => document.getElementById(next.id)?.scrollIntoView()}
			>
				{next.label}
			</Button>
		);
	};

	return (
		<Fragment>
			<Container id={id} className={`home-section ${className ?? ''}`} fluid>
				{getPrevButton()}
				{children}
				{getNextButton()}
			</Container>
		</Fragment>
	);
};

export default HomeSection;