import { KeyboardArrowDown, KeyboardArrowUp } from '@material-ui/icons';
import React, { Fragment, useState } from 'react';
import { Col, Collapse, Container, Image, Row } from 'react-bootstrap';
import { Globe, ClipboardCheck } from 'react-bootstrap-icons';
import { getPlaceholderImg, getProductTypeTxt, ProjectColor } from '../../Modules/Common';
import { PersonalProject, WorkDetails } from '../../Modules/ProjectDetails';

interface ProjectContainerProps {
	details: PersonalProject | WorkDetails
}

const ProjectContainer = ({ details }: ProjectContainerProps) => {

	const [open, setOpen] = useState(false);

	const getLanguagesElement = (): JSX.Element | null => {
		if (details.technologies.languages) {
			return (
				<p className='m-0'>Languages: {details.technologies.languages.join(', ')}</p>
			);
		}
		else {
			return null;
		}
	};

	const getFrameworksElement = (): JSX.Element | null => {
		if (details.technologies.frameworks) {
			return (
				<p className='m-0'>Frameworks: {details.technologies.frameworks.join(', ')}</p>
			);
		}
		else {
			return null;
		}
	};

	const getOthersElement = (): JSX.Element | null => {
		if (details.technologies.other) {
			return (
				<p className='m-0'>Other: {details.technologies.other.join(', ')}</p>
			);
		}
		else {
			return null;
		}
	};

	const getTechnologiesElement = (): JSX.Element => {
		
		return (
			<Fragment>
				{getLanguagesElement()}
				{getFrameworksElement()}
				{getOthersElement()}
			</Fragment>
		);
	};

	const getProductElement = (): JSX.Element | null => {
		if (details.product)
			return (
				<Container fluid style={{ textAlign: 'right' }}>
					<a href={details.product.url} target='_blank' rel='noreferrer'>
						Visit {getProductTypeTxt(details.product.type)} &gt;
					</a>
				</Container>
			);
		else {
			return null;
		}
	};

	const getWebsiteElement = (): JSX.Element | null => {
		
		if (details.websiteUrl) {
			
			return (
				<Container fluid className='p-0'>
					<Globe className='mb-1' />
					<span className='mx-2'>Website:</span>
					<a href={details.websiteUrl} target='_blank' rel='noreferrer'>
						{details.websiteUrl}
					</a>
				</Container>
			);
		}
		else {
			return null;
		}
	};
	
	const getLicenseElement = (): JSX.Element | null => {
		
		if (details.isPersonal && details.licenseDetails) {
			
			return (
				<Container fluid className='p-0'>
					<ClipboardCheck className='mb-1' />
					<span className='mx-2'>{details.licenseDetails}</span>
				</Container>
			);
		}
		else {
			return null;
		}
	};

	return (
		<Row className='project-row'>
			<Container className='project-title-container'>
				<Row onClick={() => setOpen(!open)}>
					<Col xs='auto' className='p-0'>
						<Image
							alt={`Image of "${details.title}"`}
							src={details.modalImg ?? getPlaceholderImg(ProjectColor.GeraldinePink)}
						/>
					</Col>
					<Col>
						<h4>{details.title}</h4>
						<h6>{details.subtitle}</h6>
					</Col>
					<Col style={{ display: 'flex', alignItems: 'center' }} xs='auto'>
						{ open ? <KeyboardArrowUp /> : <KeyboardArrowDown /> }
					</Col>
				</Row>
			</Container>
			<Collapse in={open}>
				<Container className='py-3'>
					{details.description}
					<hr />
					{getTechnologiesElement()}
					{getProductElement()}
					<hr />
					{getWebsiteElement()}
					{getLicenseElement()}
				</Container>
			</Collapse>
		</Row>
	);
};

export default ProjectContainer;